<template>
  <div class="dictionary-edit">
    <modal
      :title="title"
      :visible="visible"
      width="33.542vw"
      height="17.76vw"
      @close="$emit('close')"
    >
      <img
        slot="modalBg"
        class="modal-bg"
        src="../../assets/img/components/img-modal-1.png"
      />
      <a-form-model ref="ruleForm" :model="form" :rules="rules">
        <a-form-model-item label="字典名称：" prop="dictName">
          <a-input
            v-model="form.dictName"
            class="default-input"
            autocomplete="off"
          />
        </a-form-model-item>
        <a-form-model-item label="字典code：" prop="dictCode">
          <a-input
            v-model="form.dictCode"
            class="default-input"
            autocomplete="off"
          />
        </a-form-model-item>
      </a-form-model>
      <div class="details-operation-btn">
        <a-button class="form-button" type="default" @click="$emit('close')">
          取消
        </a-button>
        <a-button class="form-button" type="primary" @click="onSubmit">
          保存
        </a-button>
      </div>
    </modal>
  </div>
</template>

<script>
import Modal from "../../components/Modal";

import moment from "moment";

export default {
  components: {
    Modal
  },
  props: {
    // 对话框是否可见
    visible: {
      type: Boolean,
      default: false
    },
  },
  data() {
    return {
      form: {
        dictName: "",
        dictCode: ""
      },
      title: "新增信息",
      rules: {
        dictName: [
          {
            required: true,
            message: "字典名称不可为空",
            trigger: "blur"
          }
        ],
        dictCode: [
          {
            required: true,
            message: "字典code不可为空",
            trigger: "blur"
          }
        ]
      }
    };
  },
  methods: {
    moment,

    /**
     * 初始化表单值
     * @param {*} data 表单参数
     * @return {*}
     */
    initFromData(data, title) {
        this.title = title;
        this.form = data;
    },

    onSubmit() {
      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          //   alert('submit!');
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    }
  }
};
</script>

<style lang="less" scoped></style>
