<!--
 * @Description: 数据字典列表展示
 * @Autor: Zhongyu
 * @Date: 2021-05-07 10:26:43
 * @LastEditors: Zhongyu
 * @LastEditTime: 2021-05-19 13:53:52
-->
<template>
  <div class="dictionary">
    <box>
      <div class="search">
        <!-- 搜索栏 start -->
        <div class="search-body">
          <!-- 表单 layout="inline" 横向自适应布局 -->
          <a-form-model
            class="default-search-form"
            layout="inline"
            :model="searchData"
            ref="ruleForm"
            @submit="handleSubmit"
          >
            <a-form-model-item label="字典名称：" prop="dictName">
              <a-input
                v-model="searchData.dictName"
                class="default-input"
                placeholder="请输入"
              ></a-input>
            </a-form-model-item>
            <a-form-model-item label="字典code：" prop="dictCode">
              <a-input
                v-model="searchData.dictCode"
                class="default-input"
                placeholder="请输入"
              ></a-input>
            </a-form-model-item>
          </a-form-model>
        </div>
        <!-- 搜索栏 end -->

        <!-- 操作按钮 start -->
        <div class="operation-btn">
          <a-button class="operation-button" type="primary" html-type="submit">
            <a-icon type="search" /> 搜索
          </a-button>
          <a-button
            class="operation-button rear-butoon"
            type="primary"
            @click="resetForm"
          >
            <a-icon type="reload" /> 重置
          </a-button>
          <a-button
            class="operation-button rear-butoon"
            type="primary"
            @click="addDictionary"
          >
            <a-icon type="form" /> 新增
          </a-button>
          <!-- 操作按钮 end -->
        </div>
      </div>

      <!-- 内容区域 start -->
      <div class="content-body">
        <!-- 表格 start -->
        <a-table
          class="default-table"
          :pagination="false"
          :columns="columns"
          :data-source="datas"
          rowKey="no"
          :scroll="{ y: 'calc(90vh - 16.85vw)' }"
        >
          <span slot="operation" class="operation" slot-scope="text, record">
            <div class="count" @click="editDictionary(record)">
              <a-icon type="form" />
              <a>编辑</a>
            </div>
            <div class="count" @click="getDictionary(record)">
              <a-icon type="aliwangwang" />
              <a>查看</a>
            </div>
            <div class="count" @click="deleteDictionary(record)">
              <a-icon type="close" />
              <a>删除</a>
            </div>
          </span>
        </a-table>
        <!-- 表格 end -->
      </div>
      <!-- 内容区域 end -->

      <!-- 分页器 start -->
      <div class="content-pagination">
        <pagination
          ref="pagination"
          :total="500"
          @change="onPaginationChange"
        />
      </div>
      <!-- 分页器 end -->
    </box>

    <dictionary-edit
      ref="dictionaryEdit"
      :visible="editVisible"
      @close="setEditVisible(false)"
    ></dictionary-edit>
    
    <dictionary-delete
      ref="dictionaryDelete"
      :visible="deleteVisible"
      @close="closeDelete"
    ></dictionary-delete>
    
    <dictionary-details
      ref="dictionaryDetails"
      :visible="detailsVisible"
      @close="closeDetailsVisible"
    ></dictionary-details>
  </div>
</template>

<script>
import Box from "../../components/Box";
import Pagination from "../../components/Pagination";

import DictionaryEdit from "./dictionaryEdit";
import DictionaryDelete from "./dictionaryDelete";
import DictionaryDetails from "./dictionaryDetails";

/**
 * 表头 // details
 */
const columns = [
  {
    title: "字典名称",
    key: "dictName",
    dataIndex: "dictName"
  },
  {
    title: "字典code",
    dataIndex: "dictCode",
    key: "dictCode"
  },
  {
    title: "操作",
    dataIndex: "operation",
    key: "operation",
    width: "15vw",
    scopedSlots: { customRender: "operation" }
  }
];

export default {
  components: {
    Box, // 大边框
    Pagination, // 分页器
    DictionaryEdit, // 新增，修改
    DictionaryDelete, // 删除
    DictionaryDetails // 查看
  },
  data() {
    return {
      columns: columns, // 表格表头
      datas: [], // 表格数据
      editVisible: false, // 是否显示弹框
      deleteVisible: false, // 是否显示删除弹框
      detailsVisible: false,
      modalTitle: "新增信息", // 弹框头文字
      pageSize: 10, // 每页条数
      page: 1, // 当前页
      // 搜索栏值
      searchData: {
        dictName: "",
        dictCode: ""
      }
    };
  },
  mounted() {
    //设置分页数据
    this.$refs.pagination.setData(this.page, this.pageSize);
    // 加载表格数据
    this.setList();
  },

  methods: {
    setList() {
      let datas = [];
      for (let i = 0; i < this.pageSize; i++) {
        let item = {
          no: i + 1, // 序号
          dictName: "号牌种类", // 接口名称
          dictCode: 1001 + i // 接口类别
        };
        datas.push(item);
      }
      this.datas = datas;
    },

    /**
     * 分页时间
     */
    onPaginationChange(page, pageSize) {
      console.log(page, pageSize);
      this.pageSize = pageSize;
      this.setList();
    },

    /**
     * 设置弹框是否显示
     * @param {*} visible 是否显示参数
     */
    setEditVisible(visible) {
      this.editVisible = visible;
    },

    /**
     * 关闭删除弹框
     */
    closeDelete() {
      this.deleteVisible = false;
    },

    closeDetailsVisible() {
      this.detailsVisible = false;
    },

    /**
     * 添加数据字典-打开弹框
     */
    addDictionary() {
      this.setEditVisible(true);
      this.$refs.dictionaryEdit.initFromData({
        dictName: "",
        dictCode: ""
      });
    },

    /**
     * 编辑数据字典-打开弹框
     * @param {*} record 当前行数据
     */
    editDictionary(record) {
      this.setEditVisible(true);
      // 初始化弹框默认值
      this.$refs.dictionaryEdit.initFromData(record);
    },

    deleteDictionary() {
      this.deleteVisible = true;
    },

    getDictionary(record) {
      this.detailsVisible = true;
      this.$refs.dictionaryDetails.initFromData(record);
    },

    /**
     * 搜索栏搜索
     */
    handleSubmit() {
      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          alert("submit!");
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },

    /**
     * 搜索栏重置
     */
    resetForm() {
      this.$refs.ruleForm.resetFields();
    }
  }
};
</script>

<style lang="less">
@import "./index.less";
</style>
