<template>
  <div class="dictionary-details">
    <modal
      title="查看信息"
      :visible="visible"
      width="45.938vw"
      height="28.177vw"
      @close="$emit('close')"
      :visibleMask="!editVisible && !deleteVisible"
    >
      <img
        slot="modalBg"
        class="modal-bg"
        src="../../assets/img/components/img-modal-2.png"
        alt=""
        style="width: 45.938vw;height: 28.177vw;"
      />
      <div class="details-search">
        <a-form-model :model="form" :layout="formLayout">
          <a-form-model-item label="父级名称：">
            <a-input
              v-model="form.dictName"
              class="default-input"
              autocomplete="off"
              disabled
            />
          </a-form-model-item>
          <a-form-model-item label="父级code：">
            <a-input
              v-model="form.dictCode"
              class="default-input"
              autocomplete="off"
              disabled
            />
          </a-form-model-item>
        </a-form-model>

        <div class="details-operation-btn">
          <a-button
            class="operation-button reset-butoon"
            type="primary"
            @click="addDictionary"
          >
            <a-icon type="form" /> 新增
          </a-button>
        </div>
      </div>
      <div class="details-contents">
        <a-table
          class="default-table"
          :pagination="false"
          :columns="columns"
          :data-source="data"
          rowKey="no"
          :scroll="{ y: '10.469vw' }"
        >
          <span slot="operation" class="operation" slot-scope="text, record">
            <div class="count" @click="editDictionary(record)">
              <a-icon type="form" />
              <a>编辑</a>
            </div>
            <div class="count" @click="deleteDictionary()">
              <a-icon type="close" />
              <a>删除</a>
            </div>
          </span>
        </a-table>

        <a-pagination
          v-model="current"
          :total="total || 1"
          :page-size="pageSize"
          :item-render="itemRender"
          @change="onPaginationChange"
        />
      </div>
      <!-- 表格展示 end -->

      <!-- 分页 start -->
    </modal>

    <dictionary-edit
      ref="dictionaryEdit"
      :visible="editVisible"
      @close="setVisible(false)"
    ></dictionary-edit>
    <dictionary-delete
      ref="dictionaryDelete"
      :visible="deleteVisible"
      @close="closeDelete"
    ></dictionary-delete>
  </div>
</template>

<script>
import Modal from "../../components/Modal";
import DictionaryEdit from "./dictionaryEdit";
import DictionaryDelete from "./dictionaryDelete";

import moment from "moment";

//表头信息
const columns = [
  {
    title: "字典名称",
    key: "dictName",
    dataIndex: "dictName"
  },
  {
    title: "字典code",
    dataIndex: "dictCode",
    key: "dictCode"
  },
  {
    title: "操作",
    dataIndex: "operation",
    key: "operation",
    width: "10vw",
    scopedSlots: { customRender: "operation" }
  }
];

export default {
  components: {
    Modal,
    DictionaryEdit, // 新增，修改
    DictionaryDelete // 删除
  },
  props: {
    // 对话框是否可见
    visible: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      columns: columns,
      data: [],
      selectedRowKeys: [],
      selectedRows: [],
      pageSize: 7,
      total: 100,
      current: 1,
      formLayout: "inline",
      editVisible: false, // 是否显示弹框
      deleteVisible: false, // 是否显示删除弹框
      form: {
        dictName: "",
        dictCode: ""
      }
    };
  },
  mounted() {
    let data = [];
    for (let i = 0; i < this.pageSize; i++) {
      let item = {
        no: i + 1, // 序号
        dictName: "号牌种类", // 接口名称
        dictCode: 1001 + i // 接口类别
      };
      data.push(item);
    }
    this.data = data;
  },
  methods: {
    moment,
    itemRender(current, type, originalElement) {
      if (type === "prev") {
        return "上一页";
      } else if (type === "next") {
        return "下一页";
      } else if (type === "page") {
        return current;
      }
      return originalElement;
    },
    initFromData(data) {
      this.form = data;
    },
    onPaginationChange(page, pageSize) {
      console.log(page, pageSize);
    },

    /**
     * 设置弹框是否显示
     * @param {*} visible 是否显示参数
     */
    setVisible(visible) {
      this.editVisible = visible;
    },

    /**
     * 关闭删除弹框
     */
    closeDelete() {
      this.deleteVisible = false;
    },

    /**
     * 添加数据字典-打开弹框
     */
    addDictionary() {
      this.setVisible(true);
      this.$refs.dictionaryEdit.initFromData({
        dictName: "",
        dictCode: ""
      }, "新增信息");
    },

    /**
     * 编辑数据字典-打开弹框
     * @param {*} record 当前行数据
     */
    editDictionary(record) {
      this.setVisible(true);
      // 初始化弹框默认值
      this.$refs.dictionaryEdit.initFromData(record, "编辑信息");
    },

    deleteDictionary() {
      this.deleteVisible = true;
    }
  }
};
</script>

<style lang="less" scoped></style>
